<template>
    <Scroll class="listview" :data="data" ref="listview" :listen-scroll="listenScroll" @scroll="scroll" :probe-type="probeType">
      <ul>
        <li v-for="group in data" class="listgroup" ref="listgroup">
          <h2 class="list-group-title">{{group.title}}</h2>
          <ul>
            <li @click="selectItem(item)" v-for="item in group.items" class="list-group-item">
              <img v-lazy="item.avatar" alt="item.name" class="avatar">
              <span class="name">{{item.name}}</span>
            </li>
          </ul>
        </li>
      </ul>
      <div class="list-shortcut" @touchstart.stop.prevent="onShortcutTouchStart" @touchmove.stop.prevent="onShortcutTouchMove">
        <ul>
          <li v-for="(item,index) in shortcutList" :data-index="index" :class="{'current':currentIndex===index}">
            {{item}}
          </li>
        </ul>
      </div>
      <div class="list-fixed" ref="fixed" v-show="fixedTitle">
        <div class="fixed-title">{{fixedTitle}} </div>
      </div>
      <div class="loading-container" v-show="!data.length">
        <loading></loading>
      </div>
    </Scroll>
</template>

<script>
  import Scroll from './scroll'
  import {getData} from '@/common/js/dom'
  import loading from '@/base/loading'
  const anchorHeight = 17
  const TITLE_HEIGHT = 40
  export default {
    name: "listview",
    props: {
      data: {
        type: Array,
        default: []
      }
    },
    data() {
      return {
        currentIndex: 0,
        scrollY: -1,
        diff: -1
      }
    },
    created() {
      this.touch = {}
      this.listenScroll = true
      this.probeType = 3
    },
    components: {
      Scroll,
      loading
    },
    computed: {
      shortcutList() {
        return this.data.map((group) => {
          return group.title.substr(0, 1)
        })
      },
      fixedTitle() {
        if (this.scrollY > 0) {
          return ''
        }
        return this.data[this.currentIndex] ? this.data[this.currentIndex].title : ''
      }
    },
    methods: {
      onShortcutTouchStart(e) {
        let anchorIndex = getData(e.target, 'index')
        let firstTouch = e.touches[0]
        this.touch.y1 = firstTouch.pageY
        this.touch.anchorIndex = anchorIndex
        this._scrollTo(anchorIndex)
      },
      selectItem(item){
          this.$emit('select',item)
      },
      onShortcutTouchMove(e) {
        let firstTouch = e.touches[0]
        this.touch.y2 = firstTouch.pageY
        let delta = (this.touch.y2 - this.touch.y1) / anchorHeight | 0
        let anchorIndex = parseInt(this.touch.anchorIndex) + delta
       this._scrollTo(anchorIndex)
      },
      scroll(pos) {
        this.scrollY = pos.y
      },
      refresh(){
        this.$refs.listview.refresh()
      },
      _calculateHeight() {
        this.listHeight = []
        const list = this.$refs.listgroup
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < list.length; i++) {
          let item = list[i]
          height += item.clientHeight
          this.listHeight.push(height)
        }
      },
      _scrollTo(index) {
        if (!index && index !== 0) {
          return
        }
        if (index < 0) {
          index = 0
        } else if (index > this.listHeight.length - 2) {
          index = this.listHeight.length - 2
        }
        this.scrollY = -this.listHeight[index]
        this.$refs.listview.scrollToElement(this.$refs.listgroup[index], 0)
      }
    },
    watch: {
      data() {
        setTimeout(() => {
          this._calculateHeight()
        }, 20)
      },
      scrollY(newY) {
        const listHeight = this.listHeight
        // 当滚动到顶部，newY>0
        if (newY > 0) {
          this.currentIndex = 0
          return
        }
        // 在中间部分滚动
        for (let i = 0; i < listHeight.length - 1; i++) {
          let height1 = listHeight[i]
          let height2 = listHeight[i + 1]
          if (-newY >= height1 && -newY < height2) {
            this.currentIndex = i
            this.diff = height2 + newY
            return
          }
        }
        // 当滚动到底部，且-newY大于最后一个元素的上限
        this.currentIndex = listHeight.length - 2
      },
      diff(newVal) {
        let fixedTop = (newVal > 0 && newVal < TITLE_HEIGHT) ? newVal - TITLE_HEIGHT : 0
        if (this.fixedTop === fixedTop) {
          return
        }
        this.fixedTop = fixedTop
        this.$refs.fixed.style.transform = `translate3d(0,${fixedTop}px,0)`
      }

    }
  }
</script>

<style scoped lang="stylus">
  @import '../common/stylus/variable.styl'
  .listview{
    position: absolute
    width: 100%
    height: 100%
    overflow :hidden
    display: flex
    flex-direction :column
    .listgroup{
      height:100%
      display: flex
      flex-direction :column
      h2{
        font-weight:bold
        border-left:6px solid #007aff;
        background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
        height:40px
        line-height:40px
        color:#FFFFFF
        padding-left :15px
        letter-spacing:2px
      }
      .list-group-item{
        display :flex
        align-items:center
        flex-direction :row
        height:90px;
        box-sizing :border-box
        border-bottom :1px solid rgba(222,222,222,.2)
        img{
          height:60px
          width:60px
          margin:16px
          border-radius :50%
        }
        span{
          color: #020402
        }
      }
    }
    .list-shortcut{
      position: fixed;
      right:5px
      height:350px
      width:20px
      top: 50%

      margin-top: -195px


  ul{
        display: flex
        flex-direction :column
        justify-content :center
        padding:2px
        border:1px solid rgba(222,222,222,0.5)
        border-radius :15px
        li{
          text-align :center
          font-size:14px
          margin-bottom:3px
        }
        .current{
          color:black
        }
      }
    }
  }

  .list-fixed {
    position: absolute
    top: 0
    left: 0
    width: 100%
    .fixed-title {
        font-weight:bold
        border-left:6px solid #007aff;
        background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
        height:40px
        line-height:40px
        color:#FFFFFF
        padding-left :15px
        letter-spacing:2px
    }
  }
  .loading-container
    position: absolute
    width: 100%
    top: 50%
    transform: translateY(-50%)
</style>



